.loginScreen {
  position: relative;
  height: 100vh;
  background: url("https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/93c34f94-56c8-40a7-8b2e-b4aac6427977/GB-en-20210125-popsignuptwoweeks-perspective_alpha_website_medium.jpg")
    center no-repeat;
  background-size: cover;
}
.loginScreen__logo {
  position: fixed;
  left: 0;
  width: 8rem;
  object-fit: contain;
  padding-left: 1.5rem;
}
.loginScreen__button {
  position: fixed;
  right: 1rem;
  top: 1.2rem;
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background-color: #e50914;
  cursor: pointer;
  font-weight: 700;
  border: none;
  outline: none;
  border-radius: 6px;
}
.loginScreen__gradient {
  width: 100%;
  height: 100vh;
  z-index: 1;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.7) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.7) 100%
  );
}
.loginScreen__body {
  position: absolute;
  top: 25%;
  z-index: 1;
  color: white;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  left: 0;
  right: 0;
}
.loginScreen__body > h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}
.loginScreen__body > h2 {
  font-size: 1.7rem;
  font-weight: 400;
  margin-bottom: 30px;
}
.loginScreen__body > h3 {
  font-size: 0.8rem;
  font-weight: 400;
}
.loginScreen__input {
  margin: 20px;
}
.loginScreen__input > form > input {
  padding: 10px;
  outline: none;
  border: none;
  height: 30px;
  width: 30%;
  max-width: 600px;
}
.loginScreen__getStarted {
  padding: 0.8rem 20px;

  font-size: 1rem;
  color: white;
  background-color: #e50914;
  border: none;
  outline: none;
  font-weight: 600;
  cursor: pointer;
}
