.banner {
  position: relative;
  color: white;
  height: 450px;
  object-fit: contain;
}
.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 160px;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner__button {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 0.5rem 2rem 0.5rem 2rem;
  background-color: rgba(51, 48, 48, 0.5);
  margin-right: 1rem;
}
.banner__button:hover {
  color: black;
  background-color: #e6e6e6;
  transition: all 0.7s;
}
.banner--fadeBottom {
  height: 7.5rem;

  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(53, 52, 52, 0.61),
    rgb(1, 27, 19)
  );
}
